// ---- COLORS -----
$color-primary: #55c57a;
$color-primary-light: #7ed56f;
$color-primary-dark: #28b485;

$color-secondary-light: #ffb900;
$color-secondary-dark: #ff7730;

$color-tertiary-light: #2998ff;
$color-tertiary-dark: #5643fa;

$color-grey-dark: #777;
$color-grey-dark--2: #999;
$color-grey-dark--3: #333;

$color-grey-light: #bbb;

$color-black: #000;
$color-white: #fff;
$color-grey-light-1: #f7f7f7;
$color-grey-light-2: #eee;

// ---- GRID ----
$grid-width: 114rem;
$vertical-gap: 8rem;
$vertical-gap--small: 6rem;
$horizontal-gap: 6rem;

// ---- FONT SIZES ---
$default-font-size: 1.6rem;

// ---- BUTTONS BOX SHADOW TRANSFORMATION ----
$btn-hover-box-shadow: 0 1rem 2rem
  rgba(
    $color: #000000,
    $alpha: 0.15,
  );

$btn-active-box-shadow: 0 0.5rem 1rem
  rgba(
    $color: #000000,
    $alpha: 0.15,
  );

// -------- COMPOSITION-BOX-SHADOW- TRASFORMATION -----
$img-hover-box-shadow: 0 2.5rem 4rem
  rgba(
    $color: #000000,
    $alpha: 0.5,
  );
