.card {
  // FUNCTIONALITY

  // Impostiamo la stessa altezza della sard-side perche essendo quelle posizionate assolutamente il suo contenitore collassa ad altezza 0.
  height: 52rem;

  // Questa proprietà deve essere messa nell'elemento genitore
  -moz-perspective: 150rem;
  perspective: 150rem;
  position: relative;

  &__side {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    font-size: 2rem;
    width: 100%;
    height: 52rem;
    transition: all 800ms ease;
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba($color: #000000, $alpha: 0.15);
    // Nasconede la parte posteriore della card e la rende visibile non appena ruota
    backface-visibility: hidden;

    &--front {
      background-color: white;
    }

    &--back {
      //   Essendo la parte posteriore essa parte già ruotata di 180°
      transform: rotateY(180deg);

      &-1 {
        background-image: linear-gradient(
          to right bottom,
          $color-secondary-light,
          $color-secondary-dark
        );
      }
      &-2 {
        background-image: linear-gradient(
          to right bottom,
          $color-primary-light,
          $color-primary-dark
        );
      }
      &-3 {
        background-image: linear-gradient(
          to right bottom,
          $color-tertiary-light,
          $color-tertiary-dark
        );
      }
    }
  }

  &:hover &__side--front {
    // Facciamo girare la card di 180 gradi sull'asse verticale
    transform: rotateY(-180deg);
  }

  &:hover &__side--back {
    // Facciamo girare il lato posteriore in modo che torni dritta per essere letta
    transform: rotateY(0);
  }

  // FRONT SIDE STYLING
  &__picture {
    background-size: cover;
    height: 23rem;

    // Questa proprietà serve a mischiare il linear gradient con l'immagine
    background-blend-mode: screen;
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);

    &--1 {
      background-image: linear-gradient(
          to right bottom,
          $color-secondary-light,
          $color-secondary-dark
        ),
        url(../../img/nat-5-sm.jpg);

      @media (min-width: 56.25em) and (min-resolution: 192dpi) {
        background-image: linear-gradient(
            to right bottom,
            $color-secondary-light,
            $color-secondary-dark
          ),
          url(../../img/nat-5-md.jpg);
      }

      @media (min-width: 112.5em) {
        background-image: linear-gradient(
            to right bottom,
            $color-secondary-light,
            $color-secondary-dark
          ),
          url(../../img/nat-5.jpg);
      }
    }

    &--2 {
      background-image: linear-gradient(
          to right bottom,
          $color-primary-light,
          $color-primary-dark
        ),
        url(../../img/nat-6-sm.jpg);

      @media (min-width: 56.25em) and (min-resolution: 192dpi) {
        background-image: linear-gradient(
            to right bottom,
            $color-primary-light,
            $color-primary-dark
          ),
          url(../../img/nat-6-md.jpg);
      }

      @media (min-width: 112.5em) {
        background-image: linear-gradient(
            to right bottom,
            $color-primary-light,
            $color-primary-dark
          ),
          url(../../img/nat-6.jpg);
      }
    }

    &--3 {
      background-image: linear-gradient(
          to right bottom,
          $color-tertiary-light,
          $color-tertiary-dark
        ),
        url(../../img/nat-7-sm.jpg);
      @media (min-width: 56.25em) and (min-resolution: 192dpi) {
        background-image: linear-gradient(
            to right bottom,
            $color-tertiary-light,
            $color-tertiary-dark
          ),
          url(../../img/nat-7-md.jpg);
      }

      @media (min-width: 112.5em) {
        background-image: linear-gradient(
            to right bottom,
            $color-tertiary-light,
            $color-tertiary-dark
          ),
          url(../../img/nat-7.jpg);
      }
    }
  }

  &__heading {
    font-size: 2.8rem;
    font-weight: 300;
    text-transform: uppercase;
    color: white;
    position: absolute;
    top: 12rem;
    right: 2rem;
    text-align: right;
    width: 75%;
  }

  &__heading-span {
    padding: 1rem 1.5rem;
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;

    // SPAN 1
    &--1 {
      background-image: linear-gradient(
        to right bottom,
        rgba($color-secondary-light, 0.85),
        rgba($color-secondary-dark, 0.85)
      );
    }

    // SPAN 2
    &--2 {
      background-image: linear-gradient(
        to right bottom,
        rgba($color-primary-light, 0.85),
        rgba($color-primary-dark, 0.85)
      );
    }

    // SPAN 3
    &--3 {
      background-image: linear-gradient(
        to right bottom,
        rgba($color-tertiary-light, 0.85),
        rgba($color-tertiary-dark, 0.85)
      );
    }
  }

  &__details {
    padding: 3rem;

    ul {
      margin: 0 auto;
      list-style: none;
      width: 80%;
      li {
        text-align: center;
        font-size: 1.5rem;
        padding: 1rem;
        &:not(:last-child) {
          border-bottom: 1px solid $color-grey-light-2;
        }
      }
    }
  }

  // BACK SIDE STYLING
  &__cta {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    text-align: center;
  }

  &__price-box {
    color: white;
    text-align: center;
    margin-bottom: 8rem;
  }

  &__price-only {
    font-size: 1.4rem;
    text-transform: uppercase;
  }

  &__price-value {
    font-size: 6rem;
    font-weight: 100;
  }

  // @include responsive(tab-portrait)

  @media only screen and (max-width: 56.25em), only screen and (hover: none) {
    height: auto;
    border-radius: 3px;
    background-color: $color-white;
    box-shadow: 0 1.5rem 4rem rgba($color: #000000, $alpha: 0.15);

    &__side {
      position: relative;
      height: auto;
      box-shadow: none;

      &--back {
        //   Essendo la parte posteriore essa parte già ruotata di 180°
        transform: rotateY(0);
        clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
      }
    }

    &:hover &__side--front {
      // Facciamo girare la card di 180 gradi sull'asse verticale
      transform: rotateY(0);
    }

    &__details {
      padding: 0 3rem;
    }
    // BACK SIDE STYLING
    &__cta {
      position: relative;
      top: 0;
      left: 0;
      transform: translate(0);
      width: 100%;
      padding: 7rem 4rem 4rem 4rem;
    }

    &__price-box {
      margin-bottom: 3rem;
    }

    &__price-value {
      font-size: 4rem;
    }
  }
}
